import React from "react";
import {Form} from "./form/Form";
import './SignUp.sass'

export const SignUp = React.forwardRef(({getUser, inputs, windowSize}, ref) => (
    <section className="signUp" ref={ref}>
        <div className="container text-center">
            <h1>
                Register to get a work
            </h1>
            <p>
                Attention! After successful registration{windowSize < 361  && <br/>} and alert, update the {windowSize > 767 && <br/>} list of users in the block from the top
            </p>
            <Form getUser={getUser} inputs={inputs}/>
        </div>
    </section>
))
