import React from "react";
import './InputFile.sass'

export const InputFile = ({ handleChange, value, error, onBlur}) => (
    <>
        <p className={`inputFileHead ${error.length > 0 ? 'error' : ''}`}>Photo</p>
        <div className={`photoContainer  ${error.length > 0 ? 'error' : ''}`}>{value.name ? value.name : 'Upload your photo' }
            <label htmlFor="photo" className={error.length > 0 ? 'error' : ''}>Browse</label>
            <input
                id = {'photo'}
                type="file"
                name={'photo'}

                onChange={(e) => {
                    handleChange(e, onBlur)

                }}

            />
            <span className={`hint ${error.length > 0 ? 'error' : ''}`}>{error.length > 0 && error}</span>
        </div>

    </>
)