import React from "react";
import './Acquainted.sass'


export const Acquainted = ({windowSize, scrollToBlock}) => (
    <section className={'acquainted'}>
        <div className="container text-center">
            <h1>Let's get acquainted</h1>
            <div className="row">
                <div className="col-lg-5 col-md-4">
                    <img src="./imgs/man-laptop-v1.svg" alt="men with laptop" title={'man with laptop'}/>
                </div>
                <div className="col-lg-7 col-md-8 text-left">
                    <div className={'wrapContent'}>
                        <h2>
                            I am cool frontend developer
                        </h2>
                        <p className={'frstParagraph'}>
                            <span>We will evaluate how clean your approach to writing CSS and Javascript</span>
                            {windowSize > 991 && <br/>}
                            code is. You can use any CSS
                            and Javascript 3rd party libraries without any restriction.
                        </p>
                        <p className={'scndParagraph'}>
                            <span>If 3rd party css/javascript libraries are added to the project via </span>
                            {windowSize > 991 && <br/>}
                            <span>bower/npm/yarn you will get bonus points.If you use any task runner
                        (gulp/webpack) you will get bonus points as well. Slice service</span>
                            directory page P​SD mockup​ into HTML5/CSS3.
                        </p>
                        <button className={'button buttonScnd'} onClick={scrollToBlock}>
                            Sing up now
                        </button>
                    </div>
                </div>
            </div>

        </div>
    </section>
)