import React from "react";
import "./Form.sass"
import {Input} from "./input/Input";
import {RadioBox} from "./radioBox/RadioBox";
import {InputFile} from "./inputFile/InputFile";
import {Modal} from "./modal/Modal";



export class Form extends React.Component {

    state = {
            positions: '',
            name: '',
            email: '',
            phone: '',
            radio: 1,
            photo: '',
            formErrors: {email: '', name: '', phone: '', photo: ''},
            nameValid: false,
            emailValid: false,
            phoneValid: false,
            photoValid: false,
            formValid: false,
            token: '',
            modal: false,
            failsFromServer: [],
        }

    handleChange = ({target: {value, name, files}}, callback = () => {}) => {

        let radioButton = this.state.positions.filter(position => position.id == name).pop()
        let idOfRadioButton = radioButton != undefined && radioButton.id
        let findTextInput = this.props.inputs.filter(input => input.name === name)
        let textInput = findTextInput.length > 0 && findTextInput.pop().name

        this.setState(() => {
            switch (name) {
                case `${idOfRadioButton}`:
                    return {radio: idOfRadioButton}
                case textInput:
                    return {[textInput]: value};
                default:
                    return {[name]: files[0]}


            }
        }, callback(name, value, files)/*,this.validationFields(name, value, files)*/)
    }

    validationFields =(fieldName, value, files) =>  {
            let fieldValidationErrors = this.state.formErrors;
            let nameValid = this.state.nameValid;
            let emailValid = this.state.emailValid;
            let phoneValid = this.state.phoneValid;
            let photoValid = this.state.photoValid;
            let dot = files != null && files != undefined && files[0].name.indexOf('.')


            switch (fieldName) {
                case 'name':
                    nameValid = value.length < 2 ? 'too short' : value.length > 60 ? 'too large' : ''
                    fieldValidationErrors.name = nameValid
                    break;
                case 'email':
                    emailValid = value.length < 2 ? 'too short'
                        : value.length > 100 ? 'too large'
                            : value.match(/^(?:[a-z0-9!#$%&'*+/=?^_`{|}~-]+(?:\.[a-z0-9!#$%&'*+/=?^_`{|}~-]+)*|"(?:[\x01-\x08\x0b\x0c\x0e-\x1f\x21\x23-\x5b\x5d-\x7f]|\\[\x01-\x09\x0b\x0c\x0e-\x7f])*")@(?:(?:[a-z0-9](?:[a-z0-9-]*[a-z0-9])?\.)+[a-z0-9](?:[a-z0-9-]*[a-z0-9])?|\[(?:(?:25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)\.){3}(?:25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?|[a-z0-9-]*[a-z0-9]:(?:[\x01-\x08\x0b\x0c\x0e-\x1f\x21-\x5a\x53-\x7f]|\\[\x01-\x09\x0b\x0c\x0e-\x7f])+)\])$/i) ?
                                '' : 'invalid email'
                    fieldValidationErrors.email = emailValid
                    break;
                case 'phone':
                    phoneValid = value.match(/^[\+]{0,1}380([0-9]{9})$/i) ? '' : 'invalid number'
                    fieldValidationErrors.password = phoneValid
                    break;
                case 'photo':
                    photoValid = (files[0].name.substr(dot + 1) === 'jpeg' || files[0].name.substr(dot + 1) === 'jpg') && files[0].size < 5000000
                        ? '' : 'photo must be jpg or jpeg format and maxim size 5 mb'
                    fieldValidationErrors.password = photoValid
                    break;
                default:
                    break;
            }
            this.setState({
                    formErrors: fieldValidationErrors,
                    nameValid,
                    emailValid,
                    phoneValid,
                    photoValid,
                }, this.validateForm(nameValid, emailValid, phoneValid, photoValid)
            );
    }

    validateForm = (...params) => {
        console.log(params)
        let count = 0
        params.map(param => param === '').forEach(param => {
            param === true ? count++ : count--
            console.log(param === true)
        })
        count === 4 ? this.setState({formValid: true}) : this.setState({formValid: false})

    }

    handleSubmit = async(e, getUser) => {
        e.preventDefault()
        let form = new FormData()

        form.append('name', this.state.name)
        form.append('email', this.state.email)
        form.append('phone', this.state.phone)
        form.append('position_id', this.state.radio)
        form.append('photo', this.state.photo)
        form.append('token', this.state.token)

        let gotNewUser = await fetch('https://frontend-test-assignment-api.abz.agency/api/v1/users',
            {
                method: 'POST',
                body: form,
                headers: {
                    'Token': this.state.token,
                },
            });

        let result = await gotNewUser.json();
        this.setState({failsFromServer: result})


        await getUser(`https://frontend-test-assignment-api.abz.agency/api/v1/users?page=1&count=${window.innerWidth > 767 ? 6 : 3}`)
        this.setState({
            name: '',
            email: '',
            phone: '',
            radio: 1,
            photo: '',
            modal: true,
            nameValid: false,
            emailValid: false,
            phoneValid: false,
            photoValid: false,
            formValid: false,
        })
        this.validateForm(1, 1, 1, 1)
    }

    componentDidMount() {
        fetch('https://frontend-test-assignment-api.abz.agency/api/v1/token')
            .then(res => res.json())
            .then(res => this.setState({token: res.token}))

        fetch('https://frontend-test-assignment-api.abz.agency/api/v1/positions')
            .then(res => res.json())
            .then(res => this.setState({positions: res.positions}))
    }

    modalClose = (e) => {
        e.preventDefault()
        this.setState({modal: false})
    }

    render() {

        let {inputs, getUser} = this.props

        return (
            <form onSubmit={(e) => this.handleSubmit(e, getUser)}>
                <div className="row">
                    <div className="col-lg-6 offset-lg-3 col-md-8 offset-md-2">
                        {inputs.map((input, i) => (
                            <Input
                                type={input.type}
                                name={input.name}
                                label={input.label}
                                placeholder={input.placeholder}
                                value={this.state[input.name]}
                                handleChange={this.handleChange}
                                hint={input.hint}
                                error={this.state[`${input.name}Valid`]}
                                key={i}
                                onBlur = {()=>this.validationFields(input.name, this.state[input.name])}
                            />
                        ))}
                        <RadioBox
                            values={this.state.positions}
                            handleChange={this.handleChange}
                            position={this.state.radio}
                        />
                        <InputFile
                            value={this.state.photo}
                            handleChange={this.handleChange}
                            error={this.state.photoValid}
                            onBlur = {this.validationFields}
                        />
                        <button
                            className={`button ${this.state.formValid ? 'buttonFrst' : 'buttonDis'}`}
                            disabled={!this.state.formValid}
                        >
                            Sing up now
                        </button>
                        {this.state.modal && <Modal close={this.modalClose} requestFromServer={this.state.failsFromServer}/> }
                    </div>
                </div>
            </form>
        )
    }
}