import React, {useEffect, useLayoutEffect} from 'react';
import {Header} from "./data/components/header/Header";
import {TestAsigment} from "./data/components/testAsigment/TestAsigment";
import {Acquainted} from "./data/components/acquainted/Acquainted";
import {Users} from "./data/components/users/Users";
import {SignUp} from './data/components/signUp/SignUp'
import {Footer} from "./data/components/footer/Footer";

import './data/fonts/fonts.sass'
import 'bootstrap/dist/css/bootstrap.min.css'
import './App.sass';

let obj = {
    name: 'Sasha',
    surname: 'Molosh',
    getFullName: () => {

    }

}

function App({store: {inputs, nav}}) {

    let signUp = React.createRef()
    let [buttonMore, setButtonMore] = React.useState(true)
    let [users, setUsers] = React.useState([])
    let [size, setSize] = React.useState(0)
    let [count, setCount] = React.useState(window.innerWidth > 767 ? 6 : 3)


    useLayoutEffect(() => {
        function windowSize() {
            setSize(window.innerWidth);
        }

        window.addEventListener('resize', windowSize);
        windowSize()

        return () => window.removeEventListener('resize', windowSize);
    }, []);

    let imgStatus = (photo) => new Promise((resolve, reject) => {
        const tmp = new Image();
        tmp.src = photo;
        tmp.onload = function () {
            resolve(200);
        };
        tmp.onerror = function () {
            reject(404);
        };
    })

    let requestGetUsers = async (url = `https://frontend-test-assignment-api.abz.agency/api/v1/users?page=1&count=${count}`) => {

        let response = await fetch(url);
        let commits = await response.json();
        let intermediateArray = []

        await Promise.all(
            commits.users.map(async (user) => {
                let {id, photo, name, position, email, phone} = user

                try {
                    (await imgStatus(photo) === 200) && intermediateArray.push({
                        id,
                        photo,
                        name,
                        position,
                        email,
                        phone
                    })

                } catch {
                    intermediateArray.push(
                        {
                            id,
                            photo: 'https://irecommend.ru/sites/default/files/imagecache/copyright1/user-images/875287/KWisospOp6X8AlxD9TKrRg.jpg',
                            name,
                            position,
                            email,
                            phone
                        }
                    )
                }
            })
        )

        setUsers(intermediateArray)
        setCount(window.innerWidth > 767 ? count += 6 : count += 3)

        if (!commits.links.next_url) {
            setButtonMore(false)
        } else {
            setButtonMore(true)
        }
    }

    let scrollToBlock = block => {
        let timer = setInterval(function () {
            if (window.pageYOffset < block.current.offsetTop) {
                window.scrollBy(0, 100);
            } else {
                clearInterval(timer)
            }

        }, 20);
    };

    useEffect(() => {
        requestGetUsers()

    }, [])


    return (
        <>
            <Header
                links={nav}
                windowSize={size}
            />
            <TestAsigment
                windowSize={size} s
                scrollToBlock={() => scrollToBlock(signUp)}
            />
            <Acquainted
                windowSize={size}
                scrollToBlock={() => scrollToBlock(signUp)}
            />
            <Users
                users={users.length > 0 && users}
                onClick={() => requestGetUsers()}
                hide={buttonMore}
            />
            <SignUp
                inputs={inputs}
                getUser={requestGetUsers}
                windowSize={size}
                ref={signUp}
            />
            <Footer/>
        </>
    );
}

export default App
