import React from "react";
import {BrowserRouter} from "react-router-dom";
import {HeaderLink} from "./HeaderLink";
import './Header.sass'
import {SideBar} from "./SideBar/SideBar";


export const Header = ({links, windowSize}) => {

    let [open, setOpen] = React.useState(false)

    let toggleSideBar = () => setOpen(!open)

        return (
        <BrowserRouter>
            <header>
                <div className="container">
                    <div className="row">
                        <div className="col-lg-2 col-md-3 col-6">
                            <img src="./imgs/logo.svg" alt="logotype" title={'logotype'}/>
                        </div>
                        {windowSize > 991 ? (
                            <div className="offset-lg-4 col-lg-6">
                                <ul>
                                    {links.map(({link, path, id}) => (
                                        <HeaderLink link={link} path={path} key={id}/>
                                    ))}
                                </ul>
                            </div>
                        ) : (
                            <>
                                <div
                                    className={
                                        "offset-md-8 col-md-1 offset-sm-3 col-sm-3 col-3 offset-3 button_open"
                                    }
                                >
                                    <img
                                        src={'./imgs/menuIcon.svg'}
                                        alt="menu"
                                        title={"open menu"}
                                        onClick={toggleSideBar}
                                    />
                                </div>
                                {/*{open && <SideBar links={links} close={toggleSideBar}/>}*/}
                                <SideBar links={links} close={toggleSideBar} open={open}/>
                            </>
                        )}


                    </div>
                </div>
            </header>
        </BrowserRouter>
    )

}

