import React from "react";
import './RadioBox.sass'


export const  RadioBox = ({values, handleChange, position}) => (

    <div className={'radioBox'}>
        <p className={'position'}>Select your position</p>
        {values.length > 0 && values.map((value, i) => (
                <label key={i} className="container" htmlFor={value.id}>{value.name}
                <input
                        type="radio"
                        value={value.id}
                        name = {value.id}
                        id={value.id}
                        onChange={handleChange}
                        checked={value.id === position}

                    />
                        <span className="checkmark"></span>
                </label>
        ))}
    </div>
)