import React from "react";
import './SideBar.sass'
import {HeaderLink} from "../HeaderLink";

/*${props.open ? '_open' : ''}*/

export const SideBar = ({links, close, open}) => (
    <div
        className={`wrap_side_bar ${open ? 'wrap_side_bar_open' : ''}`}
         onClick={(e) => {
             e.target.className === 'wrap_side_bar wrap_side_bar_open' && close()
             }
         }
    >
        <div className="side_bar">
            <div className="logo">
                <img src="./imgs/logo.svg" alt="logo" title={'logo'}/>
            </div>
            
            <div className="links">
                {links.map(({link, path, id}) => (
                    <HeaderLink link={link} path={path} key={id}/>
                ))}
            </div>
            <div className="links">
                {links.map(({link, path, id}) => (
                    <HeaderLink link={link} path={path} key={id}/>
                ))}
            </div>
            <div className="links">
                {links.map(({link, path, id}) => (
                    <HeaderLink link={link} path={path} key={id}/>
                ))}
            </div>

        </div>
    </div>
)



