import React from "react";
import './TestAsigment.sass'


export const TestAsigment = ({windowSize, scrollToBlock}) => {
    let br = windowSize > 767 && <br/>
    return(
        <section className={'testAsigment'}>
            <div className="sectionWrapper">
                <div className="container">
                    <div className="row">
                        <div className="col-lg-7 col-md-8">
                            <h1>
                                Test assignment <br/>
                                for Frontend Developer position
                            </h1>
                            <p>
                                We kindly remind you that your test assignment should be submitted as a link to github/bitbucket repository.

                                {windowSize > 767 &&
                                <span>
                                    Please be patient, we consider and respond to every application that meets minimum
                                    requirements. We look forward to your submission. Good luck! The photo has to scale in
                                    the banner area
                                    on the different screens
                                </span>}
                            </p>
                            <button className={'button buttonFrst'} onClick={scrollToBlock}>
                                Sing up now
                            </button>
                        </div>
                    </div>
                </div>
            </div>

        </section>
    )
}
