import React, {Suspense} from "react";
import './Users.sass'
import {Loader} from "./loader/Loader";
/*import {RenderUsers} from "./RenderUsers";*/


const User = React.lazy((() => new Promise(resolve => {
    setTimeout(() => {
        resolve(import('./User'));
    }, 100000);
})))

export const Users = ({users, onClick, hide}) => (


    <section className={'users'}>
        <div className="container text-center">
            <h1>Our cheerful users</h1>
            <p className={'attention'}>Attention! Sorting users by registration date</p>
            <div className="row">
                {users.length > 0 && users.map((user, i) => (
                    <React.Suspense fallback={<Loader/>} key={user.id}>
                        <User photo={user.photo} name={user.name} email={user.email} phone={user.phone}/>
                    </React.Suspense>
                ))}
            </div>
            {hide && <button className={'button buttonFrst'} onClick={onClick}>Show more</button>}
        </div>


    </section>
)
