import React from "react";
import './Modal.sass'

export const Modal = ({close, requestFromServer}) => {

    return (
        <>
            <div className={'modal'}>
                <div className="modalBody">
                    <span onClick={close}>&#215;</span>
                   <h3>{requestFromServer.success ? 'Congratulations' : 'Oops' }</h3>
                    <p>{requestFromServer.message}</p>
                    {requestFromServer.fails && Object.values(requestFromServer.fails).map((item, i) => (
                        <p key={i}>
                            {item[0]}
                        </p>
                    ))}
                    <button className={'button buttonFrst'} onClick={close}>Great</button>
                </div>
            </div>
        </>
    )
}


