import React from "react";
import './Input.sass'


export const Input = ({type,name,label,placeholder, value, handleChange, hint, error, onBlur}) => (
    <div className={'inputWrap'}>

            <label
                className={`label ${error.length > 0 ? 'error' : ''}`}
                htmlFor = {name}
            >
                {label}
            </label>
        <input
            id={name}
            type={type}
            name={name}
            placeholder={placeholder}
            onChange={handleChange}
            value={value}
            className={error.length > 0 ? 'error' : ''}
            onBlur={onBlur}
        />
        <span className={`${error.length > 0 ? 'error' : ''} hint`}>{error.length > 0 ? error : hint}</span>
    </div>
)